body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.series-list-item {
    list-style:none
}

.series-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 100px;
}

input {
    display: inline;
    display: initial;
    margin-top: 20px;
    margin-left: 20px;
}


.App {
  text-align: center;
}

a {
  text-decoration: none;
  color: paleturquoise;
}

a:hover{
  color:palevioletred;
  text-decoration: none;
}




.show-container {
  display: flex;
  flex-direction: column;
  margin: 20px; }
  .show-container h2 {
    margin: 20px;
    text-align: left; }

.show-image-description {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-content: center; }
  .show-image-description p {
    margin-top: 0;
    text-align: left;
    width: 50%; }

.show-details {
  display: flex;
  flex-direction: column;
  float: left; }

